.best-photo-model-0002 {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: 100px 60px repeat(2, 1fr) 100px;
  &__headline-title {
    grid-column: span 10;
  }
  &__headline-descr {
    font-size: 16px;
    grid-column: span 5;
  }
  .img {
    min-height: 270px;
    &:nth-child(3) {
      grid-column: span 5;
      grid-row: span 2;
      background-position: 0 80%;
    }
    &:nth-child(4) {
      grid-column: span 5;
      background-position: 0 30%;
    }
    &:nth-child(5) {
      grid-column: span 2;
    }
    &:nth-child(6) {
      grid-column: span 4;
    }
    &:nth-child(7) {
      grid-column: span 4;
      background-position: 0 25%;
    }
    &:nth-child(5),
    &:nth-child(6),
    &:nth-child(7) {
      grid-row: span 4;
    }
  }
}
