}

.model-page--horizontal::after {
  top: 0;
}

.model-page--horizontal-mode-1::before, .model-page--horizontal-mode-1::after {
  left: 0;
  width: 100%;
}

.model-page--horizontal-mode-1::before {
  bottom: 0;
  height: 80%;
}

.model-page--horizontal-mode-1::after {
  top: 0;
  height: 20%;
}

.model-page--horizontal-mode-2::before, .model-page--horizontal-mode-2::after {
